import { CssBaseline } from "@mui/material";
import TopContent from "./components/TopContent";
import ImgContent from "./components/ImgContent";
import Flexible from "./components/Flexible";
import Feature from "./components/Feature";
import ForDesigner from "./components/ForDesigner";
import PricingPlan from "./components/PricingPlan";
import Question from "./components/Questions";
import DashboardImg from "./components/DashboardImg";
import PurchaseNow from "./components/PurchaseNow";
import Footer from "./components/Footer";
import TopLine from "./components/TopLine";
import {useEffect, useRef, useState} from "react";

function App() {
    const AppContainer = document.getElementsByClassName('App')
    let AppRef = useRef('');
        const [appTop, setAppTop] = useState(0);
        const [fullHeight, setFullHeight] = useState(0)
    useEffect(()=>{
        window.addEventListener('load', ()=>{
            return (
                setAppTop(-AppContainer[0].getBoundingClientRect().top),
                setFullHeight(AppContainer[0].getBoundingClientRect().height - window.innerHeight)
            )
        })
        window.addEventListener('resize', ()=>{
            return (
                setAppTop(-AppContainer[0].getBoundingClientRect().top),
                setFullHeight(AppContainer[0].getBoundingClientRect().height - window.innerHeight)
            )
        })
        window.addEventListener('scroll', ()=>{
            return (
                setAppTop(-AppContainer[0].getBoundingClientRect().top),
                setFullHeight(AppContainer[0].getBoundingClientRect().height - window.innerHeight)
            )
        })

    }, [])
  return (
    <div ref={AppRef} className="App">
      <CssBaseline />
      <TopLine line={appTop/fullHeight}/>
      <TopContent />
      <ImgContent />
      <Flexible />
      <Feature />
      <ForDesigner />
      <PricingPlan />
      <Question />
      <DashboardImg/>
      <PurchaseNow />
      <Footer />
    </div>
  );
}

export default App;
