import React from "react";
import {Box} from '@mui/material';


export default function TopLine({line}) {
    return (
        <Box sx={{
            position: 'fixed',
            top: 0,
            left: 0,
            height: '3px',
            width: '100%',
            background: '#fa541c',
            zIndex: 1000,
            transition: 'all 0.4s linear',   
            transform: `scaleX(${line}) translateZ(0px)`,
            transformOrigin: '0%',
        }}>

        </Box>
    )
}