import { Box, Container, Grid, Typography } from "@mui/material";
import React, { useState } from "react";

export default function ImgContent() {
  const [width, setWidth] = useState(window.innerWidth);
  window.addEventListener("resize", () => {
    setWidth(window.innerWidth);
  });
  return (
    <Box sx={{}}>
      <Container>
        <Grid container>
          <Grid
            item
            xs={12}
            sx={{
              my: 10,
              background: "#F9FAFB",
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: "24px",
            }}
          >
            <Box sx={{width: "75%", maxWidth: "720px"}}>
              <img
                src="	https://zone-ui.vercel.app/assets/images/home/new_start.png"
                style={{ width: "100%", maxWidth: "720px" }}
                alt=""
              />
              <Typography
                sx={{
                  my: 4,
                  fontWeight: 700,
                  textAlign: 'center',
                  fontSize:
                    width > 1200
                      ? "3rem"
                      : width > 900
                      ? "2.75rem"
                      : width >= 600
                      ? "2.5rem"
                      : "2rem",
                }}
              >
                The <span style={{ color: "#fa541c" }}>ZONE</span> UI Kit
              </Typography>
              <Typography color="#637382" sx={{ mb: 5, textAlign: 'center'}}>
                The ZONE is built on top of MUI, a powerful library that
                provides flexible, customizable, and easy-to-use components.
              </Typography>
            </Box>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
}
